:root {
    --color-dark: #27292a;
    --color-darkest: #1f2122;
    --color-light: #fff;
    --color-primery: #cc0249;
    --color-start: yellow;

    --color-bg-body: var(--color-darkest);

    --distence-primary: 50px;
    --padding-primary: var(--distence-primary);
    --margin-primary: var(--distence-primary);
    --border-radius-primary: calc(var(--distence-primary) / 2);
    
    --font-family-primary: 'Poppins', sans-serif;
    --font-weight-bold: 900;
    --font-weight-normal: 400;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&amp;display=swap');

body {
    background-color: var(--color-bg-body);
    font-family: var(--font-family-primary);
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}