.gaming-library-card {
    flex: 1 1 100%;
}

.gaming-library-card ul {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-primery);
    padding: calc(var(--padding-primary) / 2) 0;
}

.gaming-library-card ul li img {
    border-radius: 16px;
}

.gaming-library-card h4 {
    font-size: 1rem;
}